const $ = jQuery;

function collapse(item) {
  item.animate({height: 0}, 300);
}

function expand(item) {
  item.css('height', 'auto');
  const height = item.height();
  item.css('height', 0).animate({height: height}, 300, function() {
    item.css('height', 'auto');
  });
}

function setScrollHeader() {
  const st = $(window).scrollTop();
  if (st > 0) {
    $('.header').addClass('has-scroll');
  } else {
    $('.header').removeClass('has-scroll');
  }
};

function getImgSliderSlide(imgPath) {
  return '<div class="swiper-slide"><div class="img-slide"><div class="img-slide__bg"></div><div class="img-slide__main" data-swiper-parallax-opacity="0.2"><div class="img-slide__img left"><img src="'+imgPath+'"></div><div class="img-slide__img right"><img src="'+imgPath+'"></div></div></div></div>';
} 

$(() => {
  $('.pcard__toggle').click(function(e) {
    e.preventDefault();
    const pcard = $(this).closest('.pcard');
    const content = pcard.find('.pcard__content');
    const isActive = pcard.hasClass('is-active');
    pcard.toggleClass('is-active');
    console.log({ isActive });
    if (isActive) {
      collapse(content);
    } else {
      expand(content);
    }
  });

  const bannersSlider = new Swiper('.banners-slider', {
    speed: 600,
    parallax: true,
    autoplay: {
      delay: 10000,
    },
    pagination: {
      el: '.banners-slider__pagination',
      type: 'bullets',
      clickable: true,
    },
  });

  const examplesSlider = new Swiper('.examples-slider', {
    speed: 600,
    parallax: true,
    spaceBetween: 20,
    navigation: {
      nextEl: '.examples-slider__next',
      prevEl: '.examples-slider__prev',
    },
    on: {
      init: function() {
        $('.examples-slider__count').html(this.slides.length);
      },
      slideChange: function(e) {
        $('.examples-slider__current').html(e.activeIndex + 1);
      }
    },
    breakpoints: {
      1200: {
        spaceBetween: 30,
      },
    },
  });

  const imgSlider = new Swiper('.img-slider', {
    speed: 600,
    parallax: true,
    spaceBetween: 12,
    navigation: {
      nextEl: '.img-slider__next',
      prevEl: '.img-slider__prev',
    },
    on: {
      init: function() {
        $('.img-slider__count').html(this.slides.length);
      },
      slideChange: function(e) {
        $('.img-slider__current').html(e.activeIndex + 1);
      }
    },
    breakpoints: {
      1200: {
        spaceBetween: 30,
      },
    },
  });

  $('.js-open-slider-modal').click(function(e) {
    e.preventDefault();
    const modal = $(this).attr('data-modal');
    const title = $(this).attr('data-title');
    const img = $(this).attr('data-img');
    const images = img.split(', ');

    const slides = images.map(function(image) {
      return getImgSliderSlide(image);
    });

    if (title) {
      $(modal).find('.slider-modal__title').html(title);
    }

    imgSlider.removeAllSlides();
    imgSlider.appendSlide(slides);
    imgSlider.update();
    $('.img-slider__count').html(images.length);

    $(modal).addClass('is-active');
  });

  $('.js-open-modal').click(function(e) {
    e.preventDefault();
    const modal = $(this).attr('data-modal');
    const title = $(this).attr('data-title');
    const service = $(this).attr('data-service');
    const btn = $(this).attr('data-btn');

    if (title) {
      $(modal).find('.modal-form__title').html(title);
    }
    if (service) {
      $(modal).find('input[name="service"]').val(service);
    }
    if (btn) {
      $(modal).find('.modal-form__submit .btn').html(btn);
    }
    $(modal).addClass('is-active');
  });

  $('.js-close-modal').click(function(e) {
    e.preventDefault();
    $(this).closest('.modal').removeClass('is-active');
  });

  $('.js-order-form').submit(function(e) {
    e.preventDefault();
    $('#order-modal.is-active').removeClass('is-active');
    $('#thanks-modal').addClass('is-active');
  });

  $(document).keyup(function(e) {
    if (e.key === "Escape") {
      $('.modal.is-active').removeClass('is-active');
      $('.menu-active').removeClass('menu-active');
    }
  });

  setScrollHeader();

  $(window).scroll(function () {
    setScrollHeader();
  });

  $('.js-menu-toggle').click(function(e) {
    e.preventDefault();
    $('.header').toggleClass('menu-active');
  });

  $('.js-anchor').click(function(e) {
    e.preventDefault();
    const top = $($(this).attr('href')).offset().top - 100;
    $('.menu-active').removeClass('menu-active');
    $('html, body').animate({scrollTop: top}, 500);
  });
});